.cart {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 9999;
}

.cart__overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0,0.7);
}

.cart__window {
  position: relative;
  margin: 80px auto 0 auto;
  background-color: rgba(255,255,255,0.8);
  max-width: 700px;
  padding: 20px;
}

.cart__close {
  position: absolute;
  top: 20px;
  right: 20px;
  cursor: pointer;
}

.cart__close:hover {
  color: #e27878;
}

.cart__table {
  width: 100%;
  border-collapse: collapse;
}

.cart__money-col {
  width: 100px;
}

.cart__total-section {
  padding-top: 20px;
}

.cart__total-text {
  text-align: right;
  padding-right: 20px;
  padding-top: 20px;
}

.cart__total-price {
  padding-top: 20px;
}

.cart__checkout {
  border: solid 2px #489648;
  color: #489648;
  border-radius: 25px;
  padding: 5px 15px;
  margin-top: 10px;
  display: inline-block;
  cursor: pointer;
}

.cart__checkout:hover {
  background-color: #489648;
  color: white;
}

.cart__checkout-icon {
  margin-right: 5px;
}

.cart__delete {
  margin-right: 5px;
  cursor: pointer;
}

.cart__delete:hover {
  color: #e27878;
}


